.newEnquiry {
  flex: 4;
}

.addEnquiryForm {
  margin-top: 10px;
}

.addEnquiryItem {
  width: 250px;
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
}

.addEnquiryItem > label {
  color: gray;
  font-weight: 600;
  margin-bottom: 10px;
}

.addEnquiryItem > input {
  padding: 10px;
}

.addEnquiryItem > select {
  padding: 10px;
}

.addEnquiryButton {
  margin-top: 10px;
  padding: 7px 10px;
  border: none;
  border-radius: 10px;
  background-color: darkblue;
  color: white;
  font-weight: 600;
  cursor: pointer;
}
