.enquiry {
  flex: 4;
  padding: 20px;
}

.enquiryTitleContainer {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.enquiryAddButton {
  width: 80px;
  border: none;
  padding: 5px;
  background-color: teal;
  color: white;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
}

.enquiryTop {
  display: flex;
}

.enquiryTopLeft,
.enquiryTopRight {
  flex: 1;
}

.enquiryTopRight {
  padding: 20px;
  margin: 20px;
  -webkit-box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
}

.enquiryInfoImg {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 20px;
}

.enquiryInfoTop {
  display: flex;
  align-items: center;
}

.enquiryName {
  font-weight: 600;
}

.enquiryInfoBottom {
  margin-top: 10px;
}

.enquiryInfoItem {
  width: 150px;
  display: flex;
  justify-content: space-between;
}

.enquiryInfoValue {
  font-weight: 300;
}

.enquiryBottom {
  padding: 20px;
  margin: 20px;
  -webkit-box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
}

.enquiryForm {
  display: flex;
  justify-content: space-between;
}

.enquiryFormLeft {
  display: flex;
  flex-direction: column;
}

.enquiryFormLeft > label {
  margin-bottom: 10px;
  color: gray;
}

.enquiryFormLeft > input {
  margin-bottom: 10px;
  border: none;
  padding: 5px;
  border-bottom: 1px solid gray;
}

.enquiryFormLeft >select{
  margin-bottom: 10px;
}

.enquiryUploadImg{
  width: 100px;
  height: 100px;
  border-radius: 10px;
  object-fit: cover;
  margin-right: 20px;
}

.enquiryFormRight{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.enquiryUpload{
  display: flex;
  align-items: center;
}

.enquiryButton{
  border: none;
  padding: 5px;
  border-radius: 5px;
  background-color: darkblue;
  color:white;
  font-weight: 600;
  cursor: pointer;
}